























































































.note-container {
  box-sizing: border-box;
  width: 100%;
}
#description {
  resize: vertical;
}
.note-action {
  width: 100%;
  border-radius: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  bottom: 0;
  left: 0;
  position: fixed;
  background: #fff;
  z-index: 999;
}
.note-action a, .note-action a i {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  text-align: center;
  height: 3rem;
  line-height: 3rem;
  position: relative;
  color: #999;
  font-size: 1.5rem;
}
.note-content-editor {
  overflow-y:visible;
  width: 100%;
  min-height: 10rem;
  outline: none;
  position: relative;
  -webkit-user-modify: read-write-plaintext-only;
  margin-bottom: 4rem;
  -webkit-user-select:text
}
.note-content-editor:empty::before {
  content: 'Input Some Text';
  color: #999999;
  position: absolute;
}



